  grid-column-start: 3;
  grid-column-end: 8;
}

.day,
.date {
  text-transform: uppercase;
  font-weight: bold;
  font-size: var(--fs-s);
}
.date {
  transition: background-color 0.3s;
  &:hover {
    cursor: pointer;
    background-color: var(--color-bck);
  }
  &.offMonth {
    color: var(--text-color-lightest);
  }
  &.pickedDate {
    background-color: var(--color-bck);
    color: var(--text-color-lighter);
  }
  &.startDate,
  &.endDate {
    background-color: var(--text-color-lightest);
    color: white;
  }
  &.booked {
    color: var(--color-alert);
    text-decoration: underline;
  }
  &.past {
    text-decoration: line-through;
    background-color: initial;
  }
  &.future {
    background-color: var(--color-warn);
  }
  &.today {
    color: transparent;
    position: relative;
    &::after {
      content: "now";
      color: black;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate3d(-50%, -50%, 0);
      text-decoration: underline;
    }
  }
}

.month-label {
  flex-grow: 2;
  flex-basis: 0;
}

.btn {
  cursor: pointer;
  flex-grow: 1;
  flex-basis: 0;
}
.alert {
  font-weight: bold;
  font-size: var(--fs-s);
  i {
    margin-right: 5px;
  }
  color: var(--color-alert);
  text-align: left;
  padding: var(--g-m);
  display: flex;
  flex-flow: row;
  position: absolute;
  top: 100%;
  left: 0;
  opacity: 1;
  transition: opacity 0.5s;
}
.hide {
  opacity: 0;
}
</style>